import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../layout';
import Seo from '../components/seo';
import Bio from '../components/bio';
import PostCardColumn from '../components/post-card-column';
import SectionHeader from '../components/section-header';
import ProjectSection from '../components/project-section';
import Post from '../models/post';

function HomePage({ data }) {
    // 데이터 검증
    if (!data || !data.allMarkdownRemark || !data.allMarkdownRemark.edges) {
        return (
            <Layout>
                <Seo />
                <div>포스트를 찾을 수 없습니다.</div>
            </Layout>
        );
    }

    const posts = data.allMarkdownRemark.edges
        .filter(({ node }) => node && node.frontmatter) // null/undefined 필터링
        .map(({ node }) => new Post(node));

    const { author, about, language } = data.site?.siteMetadata || {};
    const projects = about?.projects || [];

    return (
        <Layout>
            <Seo />
            <Bio author={author} language={language} />
            <SectionHeader title="Posts" />
            <PostCardColumn posts={posts} />
            <SectionHeader title="Projects" />
            <ProjectSection projects={projects} />
        </Layout>
    );
}

export default HomePage;

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }, limit: 1000) {
      edges {
        node {
          id
          excerpt(pruneLength: 500, truncate: true)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            categories
            author
            emoji
            imageUrl
          }
          fields {
            slug
          }
        }
      }
    }
    site {
      siteMetadata {
        title
        description
        language
        author {
          name
          bio {
            role
            description
            thumbnail
          }
          social {
            github
            linkedIn
            email
            instaGram
          }
        }
        about {
          projects {
            title
            description
            techStack
            thumbnailUrl
            links {
              post
              github
              googlePlay
              appStore
              demo
            }
          }
        }
      }
    }
  }
`;